import React from "react";
import Link from "next/link";
import PropTypes from "prop-types";
import Container from "./Container";
import imageLogoAidesTerritoires from "../../static/images/logo-aides-territoires.png";
import imageLogoFabriqueNumerique from "../../static/images/logo-fabrique-numerique.svg";
import InjectSheet from "react-jss";
import uiConfig from "../../ui.config";

let Navigation = class extends React.Component {
  state = {
    mobileMenuOpened: false
  };
  static propTypes = {
    links: PropTypes.arrayOf(
      PropTypes.shape({
        to: PropTypes.string.isRequired,
        title: PropTypes.string.isRequired
      }).isRequired
    )
  };
  render() {
    const { classes, links } = this.props;
    return (
      <div className={classes.root}>
        <div className={classes.logos}>
          <MenuLogoAidesTerritoires />
          <MenuLogoFabriqueNumerique />
        </div>
        <div className={classes.links}>
          <MenuRight links={links} />
        </div>
      </div>
    );
  }
};
Navigation = InjectSheet({
  root: {
    padding: "2rem 0",
    display: "flex",
    justifyContent: "space-between",
    position: "relative"
  },
  "@media (max-width: 1000px)": {
    /* hide for now */
    mobileMenuToogler: {
      display: "none",
      position: "absolute",
      right: 0,
      border: "solid red 1px",
      cursor: "pointer",
      zIndex: 999
    },
    navigationContainer: {
      display: "block"
    }
  }
})(Navigation);

/**
 * MenuRight
 */
let MenuRight = ({ links, classes }) => (
  <div className={classes.root}>
    <ul className={classes.linksUl}>
      {links.map(link => (
        <li key={link.to} className={classes.linksLi}>
          <Link href={link.to}>
            <a className={classes.link}>{link.title}</a>
          </Link>
        </li>
      ))}
    </ul>
  </div>
);
MenuRight = InjectSheet({
  linksUl: {
    margin: 0,
    padding: 0,
    paddingTop: "1rem",
    listStyleType: "none",
    display: "flex",
    justifyContent: "space-around"
  },
  linksLi: { fontFamily: "BebasNeue", color: "black" },
  link: {
    color: "black",
    textDecoration: "none",
    paddingLeft: "1rem",
    fontSize: "20px"
  },
  [uiConfig.breakpoints.smallScreen]: {
    linksUl: {
      display: "block",
      paddingBottom: "1rem"
    },
    linksLi: {
      display: "block",
      textAlign: "center",
      padding: "0.5rem"
    }
  }
})(MenuRight);

let MenuLogoAidesTerritoires = ({ classes }) => {
  return (
    <img
      alt="Logo aides territoires"
      className={classes.image}
      alt="logo"
      src={imageLogoAidesTerritoires}
    />
  );
};
MenuLogoAidesTerritoires = InjectSheet({
  image: {
    height: "50px"
  },
  [uiConfig.breakpoints.smallScreen]: {
    image: {
      maxHeight: "30px"
    }
  }
})(MenuLogoAidesTerritoires);

let MenuLogoFabriqueNumerique = ({ classes }) => {
  return (
    <img
      className={classes.image}
      alt="logo"
      src={imageLogoFabriqueNumerique}
    />
  );
};
MenuLogoFabriqueNumerique = InjectSheet({
  image: {
    width: "50px",
    paddingLeft: "2rem"
  },
  [uiConfig.breakpoints.smallScreen]: {
    image: {
      maxHeight: "30px"
    }
  }
})(MenuLogoFabriqueNumerique);
export default Navigation;



